.menubar{
	width: 100%;
	height: 60px;
	position:fixed; ;
	top: 100%;
	left: 0%;
	margin-top: -60px;
	background-color: #ada2a6;
}

.ico1{
   width: 50px;
	height: 50px;
	position: absolute;
	top: 5px;
	left: 20%;
	margin-left: -25px;
	background: url(../img/fire-flame.png) center center no-repeat;
}

.ico2{
	width: 50px;
	height: 50px;
	position: absolute;
	top: 5px;
	left: 50%;
	margin-left: -25px;
	background: url(../img/home.png) center center no-repeat;
}

.ico3{
	width: 50px;
	height: 50px;
	position: absolute;
	top: 5px;
	left: 80%;
	margin-left: -25px;
	background: url(../img/lightning-electric-energy.png) center center no-repeat;
}



.ico1:hover
{
background: url(../img/fire-flame2.png) center center no-repeat;
}
.ico2:hover
{
background: url(../img/home2.png) center center no-repeat;
}
.ico3:hover
{
background: url(../img/lightning-electric-energy2.png) center center no-repeat;
}


